<template>
  <tbody>
    <tr>
      <th style="padding: 0px">{{ tittle }}</th>
      <td style="padding: 0px">
        <q-input
          v-model="model.B"
          style="max-width: 40px"
          @update:model-value="save(model.B, 'B')"
          maxlength="2"
        />
      </td>
      <td style="padding: 0px">
        <q-input
          v-model="model.M"
          style="max-width: 40px"
          maxlength="2"
          @update:model-value="save(model.M, 'M')"
        />
      </td>
      <td style="padding: 0px">
        <q-input
          v-model="model.R"
          style="max-width: 40px"
          maxlength="2"
          @update:model-value="save(model.R, 'R')"
        />
      </td>
      <td style="padding: 0px">
        <q-input
          v-model="model.NA"
          style="max-width: 40px"
          maxlength="2"
          @update:model-value="save(model.NA, 'NA')"
        />
      </td>
    </tr>
  </tbody>
</template>

<script>
import { ref } from "vue";
//import TablaCondicion from "./tablaCondicion.vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import { required /*, contarObjeto*/ } from "../../../helper/validation";
import { validateInput } from "../../../helper/validation";
//import RadioEntrada from "../../pvr/salida/salidaRadioEnt.vue";
//import SalidaRadioEnt from "../salida/salidaRadioEnt.vue";

export default {
  components: {
    //  TablaCondicion,
    // SalidaRadioEnt,
    //RadioEntrada,
  },
  props: ["tittle"],

  setup(props) {
    const model = ref({}),
      tittleSalida = ref([props.tittle]),
      keys = ref(props.tittle),
      route = useRoute(),
      visibleEntrada = route.name,
      opc = { B: "", M: "", R: "", NA: "" },
      store = useStore();

    if (!store.getters.isAction.data) {
      let variable = {
        val: "isVariableCondicionEntrada",
        valor: {
          Caucho: opc,
          Carroceria_General: opc,
          Faros_delantero: opc,
          parachoque: opc,
          Tapade_Combustible: opc,
          Espejos: opc,
          Luces_Transeras: opc,
          Extintor: opc,
          Triangulo: opc,
        },
      };

      store.dispatch("varMutuacion", variable);
    }

    if (store.getters.isAction.data.isVariableCondicionEntrada) {
      model.value = store.getters.isGetter("isVariableCondicionEntrada")[
        keys.value
      ];
    }

    const save = (val, valModel) => {
      // console.log(validateInput(event));

      console.log("validar ", validateInput(val));

      if (validateInput(val) === false) {
        model.value[valModel] = "";
      }

      let obj = {};

      let radio = props.tittle;

      obj[radio] = { ...model.value };

      //isPvrS;

      //obj = { ...obj };

      let variable = {
        val: "isVariableCondicionEntrada",
        valor: obj,
        key: radio,
      };

      store.dispatch("varMutuacionArray", variable);

      //console.log(store.getters.isGetter("isVariableCondicionSalida"));
    };

    /*const validar = (event, val) => {
      if (validateInput(event) === false) {
        model.value[val] = "";
       
    };*/

    return {
      model,

      save,
      required,
      //validar,

      tittleSalida,
      visibleEntrada,
    };
  },
};
</script>
